<template>
  <section id="alarm-cycle-page" class="df">
    <div class="wrapper df dfv">
      <div class="query-form df">
        <el-form :inline="true" :label-width="'100px'">
          <el-form-item :label="'企业名称'" prop="enterpriseName">
            <el-input size="mini" v-model="query.enterpriseName"></el-input>
          </el-form-item>

          <el-form-item :label="'服务名称'" prop="serverName">
            <el-input size="mini" v-model="query.serverName"></el-input>
          </el-form-item>

          <el-form-item :label="'规则名称'" prop="name">
            <el-input size="mini" v-model="query.name"></el-input>
          </el-form-item>
          
          <el-form-item :label="'操作用户'" prop="operatorName">
            <el-input size="mini" v-model="query.operatorName"></el-input>
          </el-form-item>

          <el-button type="primary" size="mini" @click="getCycleList()" style="margin-left: 33px;">确认</el-button>
          <el-button type="primary" size="mini" @click="query={}">清除</el-button>
        </el-form>
      </div>      

      <div class="list df dfv">
        <div class="list-header">
          <h3>告警周期列表</h3>
          <div class="btn-group">
            <el-button type='primary' size="mini" @click="addCycleToModal()">添加</el-button>
          </div>
        </div>
        <div class="table-box df dfv">
          <el-table height="100%" :data="cycleList">
            <!-- <el-table-column type="selection"></el-table-column> -->
            <el-table-column label="序号" type="index"></el-table-column>
            <el-table-column label="企业名称" prop="enterpriseName"></el-table-column>
            <el-table-column label="服务名称" prop="serverName"></el-table-column>
            <el-table-column label="规则名称" prop="name"></el-table-column>
            <el-table-column label="实时告警时效周期" prop="diffTime"></el-table-column>
            <el-table-column label="操作用户" prop="operator.name"></el-table-column>
            <el-table-column label="状态" prop="state"></el-table-column>
            <el-table-column label="创建时间" prop="updateTime" :formatter="$tableFilter('dateFormatFilter')"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <a @click="addCycleToModal(scope.row, 'detail')">查看</a>
                <a @click="updateStatus(scope.row)">{{ scope.row.state === '启用' ? '禁用' : '启用' }}</a>
                <a @click="addCycleToModal(scope.row)">修改</a>
                <a @click="deleteCycleToModal(scope.row)">删除</a>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <!-- Pagination -->
        <el-col :span="24" class="toolbar">
          <el-pagination
            layout="total, prev, pager, next,sizes, jumper"
            @current-change="pagination.handleCurrentChange"
            @size-change="pagination.pageSizeChange"
            :page-sizes="[10, 15, 50, 100]"
            :page-size="pagination.pageSize"
            :total="pagination.total"
            style="float:right;">
          </el-pagination>
        </el-col>
      </div> 
    </div>
    
    <el-dialog
      :title="setCycleForm.__type === 'detail' ? '周期详情' : '告警周期设置'"
      class="m400"
      :close-on-click-modal = "false"
      @close="setCycleVisible = false"
      :visible="setCycleVisible">
      <el-form :model="setCycleForm" :rules="setCycleRules" label-width="100px" ref="setCycle">
        <el-form-item label="企业名称" prop="enterpriseCode">
          <el-select :disabled="setCycleForm.__type === 'detail'" size="mini" v-model="setCycleForm.enterpriseCode" style="width:220px">
            <el-option v-for="(item, index) in enterPriseList" :key="index" :label="item.title" :value="item.enterpriseCode">{{ item.title }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="服务名称" prop="serverCode">
          <el-select :disabled="setCycleForm.__type === 'detail'" size="mini" v-model="setCycleForm.serverCode" @input="getUsedAlarmLevel()" style="width:220px">
            <el-option v-for="(item, index) in getServerList(setCycleForm.enterpriseCode)" :key="index" :label="item.title" :value="item.code">{{ item.title }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="规则名称">
          <span>{{ (setCycleForm.curAlarmLevel || {}).name | nullFilter }}</span>
        </el-form-item>
        <el-form-item label="操作用户">
          <span>{{ (setCycleForm.curAlarmLevel || { operator: {} }).operator.name | nullFilter }}</span>
        </el-form-item>
        <el-form-item label="时效周期" prop="diffTime">
          <el-input :disabled="setCycleForm.__type === 'detail'" v-model="setCycleForm.diffTime" size="mini" style="width:220px"></el-input> 小时
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="default" @click="setCycleVisible = false">取 消</el-button>
        <el-button size="mini" v-if="setCycleForm.__type !== 'detail'" type="primary" @click="addCycleSubmit();">确 定</el-button>
      </span>
    </el-dialog>
  </section>
</template>

<script>
export default {
  name: 'alarmCycle',
  data () {
    return {
      curUser: {strId: '123123', name: 'chenht'},
      enterPriseList: [],
      query: {

      },
      cycleList: [],
      pagination: {
        page: 1,
        pageSize: 15,
        total: 0,
        handleCurrentChange: (v) => {
          this.pagination.page = v;
          this.getCycleList();
        },
        pageSizeChange: (v) => {
          this.pagination.pageSize = v;
          this.getCycleList();
        },
      },
      setCycleVisible: false,
      setCycleForm: {},
    }
  },
  computed: {
    setCycleRules () {
      return {
        aaa: {
          message: 'sdfse',
          trigger: 'blur',
          validator (rule, value, callback) {
            if (value) {
              callback()
            } else {
              callback(new Error(''))
            }
          }
        }
      }
    },
  },
  methods: {
    getUniqueServiceList () {
      this.$api.enterpriseLevelControllerGetUniqueServiceList().then(res => {
        this.enterPriseList = res;
      })
    },
    getServerList (enterpriseCode) {
      return (this.enterPriseList.find(v => v.enterpriseCode === enterpriseCode) || {serverCodes: []}).serverCodes
    },
    getUsedAlarmLevel () {
      this.$api.enterpriseLevelControllerGetLastUse({
        enterpriseCode: this.setCycleForm.enterpriseCode,
        serverCode: this.setCycleForm.serverCode,
      }).then(res => {
        this.$set(this.setCycleForm, 'curAlarmLevel', res[0]);
      })
    },
    getCycleList () {
      this.$api.alarmCycleControllerList(Object.assign({
        currentPage: this.pagination.page,
        pageSize: this.pagination.pageSize,
      }, this.query)).then(res => {
        this.cycleList = res.list;
        this.pagination.total = res.count; 
      })
    },
    addCycleToModal (cycle, isDetail) {
      this.$nextTick(() => {
        if (cycle) {
          this.setCycleForm = Object.assign({}, cycle);
          this.getUsedAlarmLevel();
          this.setCycleForm.__type = 'modify'
        } else {
          this.setCycleForm = {
            alarms: [],
          };
          this.setCycleForm.__type = 'add'
        }
        isDetail && (this.setCycleForm.__type = 'detail')
      })
      this.setCycleVisible = true;
    },
    addCycleSubmit () {
      let query = {
        enterpriseCode: this.setCycleForm.enterpriseCode,
        serverCode: this.setCycleForm.serverCode,
        diffTime: this.setCycleForm.diffTime,
        operator: this.curUser,
        name: this.setCycleForm.curAlarmLevel.name,
        id: this.setCycleForm.id,
      }
      
      let ent = this.enterPriseList.find(v => v.enterpriseCode === query.enterpriseCode)
      let service = ent.serverCodes.find(v => v.code === query.serverCode)

      query.enterpriseName = ent.title;
      query.serverName = service.title;
      
      let method = this.setCycleForm.__type === 'add' ? 'alarmCycleControllerAdd' : 'alarmCycleControllerUpdate' ;
      this.$api[method](query).then(res => {
        console.log(res);
        this.getCycleList()
        this.setCycleVisible = false;
      })
    },
    deleteCycleToModal (cycle) {
      this.$confirm(`确定要删除该服务告警等级？<br/>注意：将要删除的服务告警等级是“XXXXX”， 确定删除吗？（包含该告警等级所有数据）`, '是否删除', 
      { type: 'warning', dangerouslyUseHTMLString: true }).then(res => {
        this.$api.alarmCycleControllerDelete({id: cycle.id}).then(res => {
          this.getCycleList()
        })
      }).catch(() => {
        
      })  
    },
    updateStatus (cycle) {
      this.$api.alarmCycleControllerUpdateState({
        id: cycle.id,
        state: cycle.state === '启用' ? '禁用' : '启用',
        enterpriseCode: cycle.enterpriseCode,
        serverCode: cycle.serverCode,
      }).then(res => {
        this.getCycleList();
      })
    }
  },
  mounted () {
    this.getUniqueServiceList()
    this.getCycleList();
  }
}
</script>

<style lang="less" scoped>
#alarm-cycle-page{
  background-color: #011838;
  .wrapper{
    margin: 10px;
    flex: 1;
    height: calc(100% - 20px);
    background-color: #0B1D3B;
  }
}
.list{
  flex: 1;
  overflow: hidden;
  padding-bottom: 10px;
  .table-box{
    flex: 1;
    overflow: hidden;
  }    
}
</style>


